import { Stores } from '../stores/stores';

export class ChartComposer {

    // AGI chart
    static getAgiChartData(): any[][] {
        let result: any = []

		result.push(['income', 'amount'])
		
		if(Stores.income.w2.value > 0) {
			result.push(['W2', parseInt(Stores.income.w2.value)])
		}
		if(Stores.income.f1099misc.amt.value > 0) {
			result.push(['Misc/Nec/Rental', parseInt(Stores.income.f1099misc.amt.value)])
		}
		if(Stores.income.rsu.amt.value > 0) {
			result.push(['RSU/Options', parseInt(Stores.income.rsu.amt.value)])
		}
		if(Stores.income.f1099b_longterm.amt.value > 0 || Stores.income.f1099b_shortterm.amt.value > 0) {
			result.push(['Capital gain', parseInt(Stores.income.f1099b_longterm.amt.value) + parseInt(Stores.income.f1099b_shortterm.amt.value)])
		}
		if(Stores.income.f1099r.amt.value > 0) {
			result.push(['Retirement dist.', parseInt(Stores.income.f1099r.amt.value)])
		}
		if(Stores.income.f1099g.amt.value > 0) {
			result.push(['Unemployment benefits', parseInt(Stores.income.f1099g.amt.value)])
		}
		if(Stores.income.farm.amt.value > 0) {
			result.push(['Farm income', parseInt(Stores.income.farm.amt.value)])
		}
		if(Stores.income.f1099_div_qualified.amt.value > 0 || Stores.income.f1099_div.amt.value > 0) {
			result.push(['Dividend', parseInt(Stores.income.f1099_div_qualified.amt.value) + parseInt(Stores.income.f1099_div.amt.value)])
		}
		if(Stores.income.f1099_int.amt.value > 0) {
			result.push(['Interest', parseInt(Stores.income.f1099_int.amt.value)])
		}
		if(Stores.income.gambling_other.amt.value > 0) {
			result.push(['Gambling/Others', parseInt(Stores.income.gambling_other.amt.value)])
		}
		if(Stores.income.espp_discount.amt.value > 0) {
			result.push(['ESPP Discount', parseInt(Stores.income.espp_discount.amt.value)])
		}
        return result
    }

    static getAgiChartConfig(): any {
        return {
            title: "Adjusted Gross Income",
            pieHole: 0.5,
            is3D: false,
            titleTextStyle: {
                fontName: 'Helvetica',
                fontSize: 14,
            }
        }
    }

    // Tax Credit Distribution chart
    static getTaxCreditDistributionChartData(): any[][] {
        let result: any = []
		result.push(['income', 'amount'])
		
		if(Stores.taxcredit.eic.amt.value > 0) {
			result.push(['EIC', parseInt(Stores.taxcredit.eic.amt.value)])
		}
		if(Stores.taxcredit.child_tax_credit.amt.value > 0) {
			result.push(['Child Tax Credit', parseInt(Stores.taxcredit.child_tax_credit.amt.value)])
		}
		if(Stores.taxcredit.dependent_credit.amt.value > 0) {
			result.push(['Dependent Tax Credit', parseInt(Stores.taxcredit.dependent_credit.amt.value)])
		}
		if(Stores.taxcredit.child_dependent_care_expense.amt.value > 0) {
			result.push(['Dependent Care Expense', parseInt(Stores.taxcredit.child_dependent_care_expense.amt.value)])
		}
		if(Stores.taxcredit.premium_tax_credit.amt.value > 0) {
			result.push(['Premium Tax Credit', parseInt(Stores.taxcredit.premium_tax_credit.amt.value)])
		}
		if(Stores.taxcredit.residential_energy_credit.amt.value > 0) {
			result.push(['Residential Energy Credit', parseInt(Stores.taxcredit.residential_energy_credit.amt.value)])
		}
		if(Stores.taxcredit.ev_credit.amt.value > 0) {
			result.push(['EV Tax Credit', parseInt(Stores.taxcredit.ev_credit.amt.value)])
		}
		if(Stores.taxcredit.foreign_tax_credit.amt.value > 0) {
			result.push(['Foreign Tax Credit', parseInt(Stores.taxcredit.foreign_tax_credit.amt.value)])
		}
        return result
    }

    static getTaxCreditDistributionChartConfig(): any {
        return {
            title: "Estimated Tax Credit",
            pieHole: 0.5,
            is3D: false,
            titleTextStyle: {
                fontName: 'Helvetica',
                fontSize: 14,
            }
        }
    }

    // Tax Advantage chart
    static getTaxAdvantageChartData(): any[][] {
        let result: any = []
		result.push(['income', 'amount'])
		
		if(Stores.income.f1099b_longterm.amt.value > 0) {
			result.push(['Long term capital gain', parseInt(Stores.income.f1099b_longterm.amt.value) ])
		}
		if(Stores.income.f1099_div_qualified.amt.value > 0) {
			result.push(['Qualified Dividend', parseInt(Stores.income.f1099_div_qualified.amt.value) ])
		}
		if(Stores.income.f1099_int_taxfree.amt.value > 0) {
			result.push(['Tax-exempt Interest', parseInt(Stores.income.f1099_int_taxfree.amt.value)])
		}
		if(Stores.income.espp_discount.amt.value > 0) {
			result.push(['ESPP Discount', parseInt(Stores.income.espp_discount.amt.value)])
		}
		if(Stores.income.f401k_match.amt.value > 0) {
			result.push(['401k/403b match', parseInt(Stores.income.f401k_match.amt.value)])
		}
		if(Stores.income.f401k_contrib.amt.value > 0) {
			result.push(['401k/403b contribution', parseInt(Stores.income.f401k_contrib.amt.value)])
		}
		if(Stores.income.ira.amt.value > 0) {
			result.push(['IRA contribution', parseInt(Stores.income.ira.amt.value)])
		}
		if(Stores.income.fsa.amt.value > 0) {
			result.push(['FSA contribution', parseInt(Stores.income.fsa.amt.value)])
		}
		if(Stores.income.hsa.amt.value > 0) {
			result.push(['HSA contribution', parseInt(Stores.income.hsa.amt.value)])
		}
		if(Stores.income.utma_gain.amt.value > 0) {
			result.push(['UTMA gain', parseInt(Stores.income.utma_gain.amt.value)])
		}
        return result
    }

    static getTaxAdvantageChartConfig(): any {
        return {
            title: "Tax Advantage Income",
            pieHole: 0.5,
            is3D: false,
            titleTextStyle: {
                fontName: 'Helvetica',
                fontSize: 14,
            }
        }
    }    

    // Deductions/Pre-Tax/Taxable
    static getDeductionTaxChartData(): any[][] {
        let result: any = []
		result.push(['income', 'amount'])
		
		let left_over = parseInt(Stores.income.agi.value) - parseInt(Stores.deduction.deduction.value[1])
		let deduction = 0
		if(left_over >= 0) {
			deduction = parseInt(Stores.deduction.deduction.value[1])
		} else {
			deduction = parseInt(Stores.deduction.deduction.value[1]) + left_over
			if(deduction < 0) {
				deduction = 0
			}
		} 
		result.push(['Pretax income', parseInt(Stores.income.pretax.value)])
		result.push(['Deductions', deduction])
		result.push(['Taxable income', parseInt(Stores.income.agi.value) - deduction])

        return result
    }

    static getDeductionTaxChartConfig(): any {
        return {
            title: "Deductions/Pre-Tax/Taxable",
            pieHole: 0.5,
            is3D: false,
            titleTextStyle: {
                fontName: 'Helvetica',
                fontSize: 14,
            }
        }
    }

    // Income Distribution
    static getIncomeDistributionChartData(): any[][] {
        let result: any[][] = []
		result.push(['income', 'amount'])
		
		let left_over = parseInt(Stores.income.agi.value) - parseInt(Stores.deduction.deduction.value[1])
		let deduction = 0
		if(left_over >= 0) {
			deduction = parseInt(Stores.deduction.deduction.value[1])
		} else {
			deduction = parseInt(Stores.deduction.deduction.value[1]) + left_over
			if(deduction < 0) {
				deduction = 0
			}
		} 
		result.push(['Pretax income', parseInt(Stores.income.pretax.value)])
		result.push(['Deductions', deduction])
        result.push(['Tax Credits', Stores.taxcredit.total_tax_credit.value])
		result.push(['Tax Due', Math.max(0, Stores.tax.due.value)])
        result.push(['Net Income', Stores.tax.taxable_income.value - Stores.tax.tax_amount.value - Stores.income.ssa_medicare_contrib.value])
		result.push(['SSA/Medicare', Stores.income.ssa_medicare_contrib.value])
        
        return result
    }

    static getIncomeDistributionChartConfig(): any {
        return {
            title: "Income Distribution",
            pieHole: 0.5,
            is3D: false,
            titleTextStyle: {
                fontName: 'Helvetica',
                fontSize: 14,
            }
        }
    }

    // Income Flow Score
    static getIncomeFlowScoreChartData(): any[][] {
        let result: any[][] = []

        result.push(['Label', 'Value'])
        let score = ((Stores.income.income.value - Stores.tax.due.value) / Stores.income.income.value) * 100
        result.push(['Score', score])
        
        return result
    }

    static getIncomeFlowScoreConfigData(): any {
        return {
            greehFrom: 80,
            greenTo: 100,
            yellowFrom: 50,
            yellowTo: 80,
            redFrom: 0,
            redTo: 50,
            minorTicks: 5,
        }
    }

    // Gross Income Source
    static getGrossIncomeChartData(): any[][] {
        let result : any[][] = []
		result.push(['income', 'amount'])
		
		if(Stores.income.w2.value > 0) {
			result.push(['W2', parseInt(Stores.income.w2.value)])
		}
		if(Stores.income.f1099misc.amt.value > 0) {
			result.push(['Misc/Nec/Rental', parseInt(Stores.income.f1099misc.amt.value)])
		}
		if(Stores.income.rsu.amt.value > 0) {
			result.push(['RSU/Options', parseInt(Stores.income.rsu.amt.value)])
		}
		if(Stores.income.f1099b_longterm.amt.value > 0 || Stores.income.f1099b_shortterm.amt.value > 0) {
			result.push(['Capital gain', parseInt(Stores.income.f1099b_longterm.amt.value) + parseInt(Stores.income.f1099b_shortterm.amt.value)])
		}
		if(Stores.income.f1099r.amt.value > 0) {
			result.push(['Retirement dist.', parseInt(Stores.income.f1099r.amt.value)])
		}
		if(Stores.income.f1099g.amt.value > 0) {
			result.push(['Unemployment benefits', parseInt(Stores.income.f1099g.amt.value)])
		}
		if(Stores.income.farm.amt.value > 0) {
			result.push(['Farm income', parseInt(Stores.income.farm.amt.value)])
		}
		if(Stores.income.f1099_div_qualified.amt.value > 0 || Stores.income.f1099_div.amt.value > 0) {
			result.push(['Dividend', parseInt(Stores.income.f1099_div_qualified.amt.value) + parseInt(Stores.income.f1099_div.amt.value)])
		}
		if(Stores.income.f1099_int.amt.value > 0) {
			result.push(['Interest', parseInt(Stores.income.f1099_int.amt.value)])
		}
		if(Stores.income.gambling_other.amt.value > 0) {
			result.push(['Gambling/Others', parseInt(Stores.income.gambling_other.amt.value)])
		}
		if(Stores.income.espp_discount.amt.value > 0) {
			result.push(['ESPP Discount', parseInt(Stores.income.espp_discount.amt.value)])
		}
		if(Stores.income.f401k_match.amt.value > 0) {
			result.push(['401k match', parseInt(Stores.income.f401k_match.amt.value)])
		}
		if(Stores.income.f1099_int_taxfree.amt.value > 0) {
			result.push(['Tax-exempt interest', parseInt(Stores.income.f1099_int_taxfree.amt.value)])
		}

        return result
    }

    static getGrossIncomeChartConfig(): any {
        return {
            title: "Gross Income",
            pieHole: 0.5,
            is3D: false,
            titleTextStyle: {
                fontName: 'Helvetica',
                fontSize: 14,
            }
        }
    }

    // Income Flow (sankey)
    static getIncomeFlowChartData(): any[][] {
        let result: any[][] = []

        result.push(['From', 'To', '$'])
		
		if(Stores.income.w2.value > 0) {
			result.push(['W2', 'Adjusted Gross Income', parseInt(Stores.income.w2.value)])

            if(Stores.income.f401k_match.amt.value > 0) {
                result.push(['W2', '401k + match', parseInt(Stores.income.f401k_match.amt.value) + parseInt(Stores.income.f401k_contrib.amt.value) ])
                result.push(['401k + match', 'Pre-tax', parseInt(Stores.income.f401k_match.amt.value) + parseInt(Stores.income.f401k_contrib.amt.value) ])
                
            }
            if(Stores.income.hsa.amt.value > 0) {
                result.push(['W2', 'HSA', parseInt(Stores.income.hsa.amt.value)])
                result.push(['HSA', 'Pre-tax', parseInt(Stores.income.hsa.amt.value)])
            }
            if(Stores.income.fsa.amt.value > 0) {
                result.push(['W2', 'FSA', parseInt(Stores.income.fsa.amt.value)])
                result.push(['FSA', 'Pre-tax', parseInt(Stores.income.fsa.amt.value)])
            }
            result.push(['W2', 'SSA + Medicare contrib.', Stores.income.ssa_medicare_contrib.value])
            result.push(['SSA + Medicare contrib.', 'Social Security/Medicare', Stores.income.ssa_medicare_contrib.value])
		}
        if(Stores.income.pretax.value > 0) {
            result.push(['Pre-tax', 'Tax-advantage Income', parseInt(Stores.income.pretax.value)])
        }

		if(Stores.income.f1099misc.amt.value > 0) {
			result.push(['Misc/Nec/Rental', 'Adjusted Gross Income', parseInt(Stores.income.f1099misc.amt.value)])
		}
		if(Stores.income.rsu.amt.value > 0) {
			result.push(['RSU/Options', 'Adjusted Gross Income', parseInt(Stores.income.rsu.amt.value)])
		}
		if(Stores.income.f1099b_longterm.amt.value > 0) {
			result.push(['long-term cap gain', 'Adjusted Gross Income' , parseInt(Stores.income.f1099b_longterm.amt.value) ])
			result.push(['Adjusted Gross Income', 'Tax-advantage cap gain', parseInt(Stores.income.f1099_div_qualified.amt.value)])
			result.push(['Tax-advantage cap gain', 'Tax-advantage Income', parseInt(Stores.income.f1099_div_qualified.amt.value)])

		}
        if(Stores.income.f1099b_shortterm.amt.value > 0) {
			result.push(['short-term cap gain', 'Adjusted Gross Income' , parseInt(Stores.income.f1099b_shortterm.amt.value)])
		}
		if(Stores.income.f1099r.amt.value > 0) {
			result.push(['Retirement dist.', 'Adjusted Gross Income', parseInt(Stores.income.f1099r.amt.value)])
		}
		if(Stores.income.f1099g.amt.value > 0) {
			result.push(['Unemployment benefits', 'Adjusted Gross Income', parseInt(Stores.income.f1099g.amt.value)])
		}
		if(Stores.income.farm.amt.value > 0) {
			result.push(['Farm income', 'Adjusted Gross Income', parseInt(Stores.income.farm.amt.value)])
		}
		if(Stores.income.f1099_div.amt.value > 0) {
			result.push(['Ordinary Dividend', 'Adjusted Gross Income', parseInt(Stores.income.f1099_div.amt.value)])
		}
		if(Stores.income.f1099_div_qualified.amt.value > 0) {
			result.push(['Qualified Dividend', 'Adjusted Gross Income', parseInt(Stores.income.f1099_div_qualified.amt.value)])
			result.push(['Adjusted Gross Income', 'Tax-advantage Dividend', parseInt(Stores.income.f1099_div_qualified.amt.value)])
			result.push(['Tax-advantage Dividend', 'Tax-advantage Income', parseInt(Stores.income.f1099_div_qualified.amt.value)])
		}

        if(Stores.income.f1099_int.amt.value > 0) {
			result.push(['Interest', 'Adjusted Gross Income', parseInt(Stores.income.f1099_int.amt.value)])
		}
		if(Stores.income.gambling_other.amt.value > 0) {
			result.push(['Gambling/Others', 'Adjusted Gross Income', parseInt(Stores.income.gambling_other.amt.value)])
		}
		if(Stores.income.espp_discount.amt.value > 0) {
			result.push(['ESPP Discount', 'Adjusted Gross Income', parseInt(Stores.income.espp_discount.amt.value)])
		}

		if(Stores.income.f1099_int_taxfree.amt.value > 0) {
			result.push(['Tax-exempt interest', 'Pre-tax', parseInt(Stores.income.f1099_int_taxfree.amt.value)])
		}
        if(Stores.income.utma_gain.amt.value > 0) {
            result.push(['UTMA gain', 'Pre-tax', parseInt(Stores.income.utma_gain.amt.value)])
		}
        if(Stores.income.ira.amt.value > 0) {
            result.push(['Adjusted Gross Income', 'IRA contribution', parseInt(Stores.income.ira.amt.value)])
            result.push(['IRA contribution', 'Pre-tax', parseInt(Stores.income.ira.amt.value)])
		}
        if(Stores.income.agi.value > 0) {
            result.push(['Adjusted Gross Income', 'Deductions', parseInt(Stores.deduction.deduction.value[1])])
            result.push(['Adjusted Gross Income', 'Taxable Income', parseInt(Stores.tax.taxable_income.value)])
            result.push(['Deductions', 'Tax-advantage Income', parseInt(Stores.deduction.deduction.value[1])])
		}
        if(Stores.deduction.f529_contribution.amt.value > 0) {
            result.push(['Taxable Income', '529 contribution', parseInt(Stores.deduction.f529_contribution.amt.value)])
            result.push(['529 contribution', 'Tax-advantage Income', parseInt(Stores.deduction.f529_contribution.amt.value)])
		}
        if(Stores.deduction.roth_ira.amt.value > 0) {
            result.push(['Taxable Income', 'Roth IRA contribution', parseInt(Stores.deduction.roth_ira.amt.value)])
            result.push(['Roth IRA contribution', 'Tax-advantage Income', parseInt(Stores.deduction.roth_ira.amt.value)])
		}
        if(Stores.tax.taxable_income.value > 0) {
            if(Stores.tax.tax_amount.value > 0)
				result.push(['Taxable Income', 'Tax Amount', Stores.tax.tax_amount.value])
			if(Stores.tax.taxable_income.value - Stores.tax.tax_amount.value > 0)
            	result.push(['Taxable Income', 'Net Income', Stores.tax.taxable_income.value - Stores.tax.tax_amount.value - Stores.income.ssa_medicare_contrib.value])
        }
        if(Stores.tax.tax_amount.value > 0) {
			if(Stores.taxcredit.total_tax_credit.value > 0)
            	result.push(['Tax Amount', 'Tax Credit', Stores.taxcredit.total_tax_credit.value])
			if(Stores.tax.due.value > 0)				
            	result.push(['Tax Amount', 'Tax Due', Stores.tax.due.value])
			if(Stores.taxcredit.total_tax_credit.value > 0)				
            	result.push(['Tax Credit', 'Tax-advantage Income', Stores.taxcredit.total_tax_credit.value])
        }

        return result
    }
}
